const ContentDetail = ({ content }) => {
  if (!content) {
    return (
      <div className="content-detail">
        <h1><span>00</span>React</h1>
        <br/>
        
      </div>
    );
  };

  // id0
  const zoroPadNum = ("00" + `${content.id}`).slice(-2);

  return (
    <div className="content-detail">
      <h1>
        <span>{zoroPadNum}</span>
        {content.title}
      </h1>
      <p>{content.body}</p>
    </div>
  );
};

export default ContentDetail;
